.App {
  width: 100%;
  height: 100vh;
  text-align: center;
  overflow: hidden;
  &::after {
    background: url('../../../public/spacee-2060x1288.jpg');
    content: "";
    opacity: 80%;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-container {
  display: grid;
  grid-template-columns: 2.5fr 1fr;
  grid-template-rows: 90px 1fr;
  row-gap: 5px;
  column-gap: 20px;
  row-gap: 20px;
  width: 100%;
  height: 100vh;
}